<template>
  <div class="container-fluid">
    <ul class="breadcrumb pl-5">
      <li class="breadcrumb-item" v-bind:class="{'text-primary':!add}" v-on:click="add = false" style="cursor:pointer">Contacts</li>
      <li class="breadcrumb-item" v-bind:class="{'text-primary':add}" v-on:click="add = true,message=false" style="cursor:pointer">Add Contact</li>
    </ul>
    <div v-if="add" class="col-md-8 mx-auto mt-3">
      <keep-alive>      
      <AddContact v-on:new-contact="newContact"/>
      </keep-alive>
    </div>
    <div v-if="!add" class="col-md-8 mx-auto mt-3">
      <input type="text" v-model="search" class="form-control" placeholder="Search contacts" />
      <div v-show="noContactList" class="alert alert-info mt-4">
        <p>You don't create a contact goto <b>add contact</b> and add contact</p>
      </div>
      <div v-show="message">
        <div class="alert alert-primary alert-dismissible fade show mt-3 p-1" role="alert">
          <button type="button" class="close" data-dismiss="alert" aria-label="Close">
            <span aria-hidden="true">&times;</span>
            <span class="sr-only">Close</span>
          </button>
          <p class="lead">{{messageContent}}</p>
        </div>
      </div>
      <div
        class="card mt-4"
        v-for="(contact,index) in filterContact"
        v-bind:key="contact._id"
      >
        <div class="card-body">
          <div class="float-left" style="display:inline;">
            <button class="btn">
                    <span class="badge badge-secondary">{{index+1}}</span>
            </button>
            <img
              v-if="contact.avatar"
              class="img-fluid"
              v-bind:src=contact.avatar
              style="width:50px;display:inline;margin-right:20px;"
            />
            <img
              v-if="!contact.avatar"
              class="img-fluid"
              src="../assets/images/avatar3.png"
              style="width:50px;display:inline;margin-right:20px;"
            />
            {{fullName(contact.firstName,contact.lastName)}}
            <span class="text-info"
              ><i class="fas fa-phone    "></i> {{ contact.phoneNumber }}</span
            >
          </div>
          <span class="float-right ">
            <routerLink v-bind:to="'/contact/'+contact._id"
              type="button"
              class="btn btn-warning btn-sm mr-4"
            >Edit 
              <i class="fas fa-edit"></i>
            </routerLink>
            <button
              @click="deleteContact(contact._id)"
              type="button"
              class="btn btn-danger btn-sm"
            >Del 
              <i class="fas fa-trash"></i>
            </button>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import AddContact from './AddContact'
  export default {
    name:"Contact",
    components:{
      AddContact
    },
    data() {
      return {
        contacts: [],
        add: false,
        user: '',
        firstName: '',
        lastName: '',
        phoneNumber: '',
        file:'',
        search:'',
        message:'false',
        messageContent:'',
      };
    },
    methods: {
      async deleteContact(id) {
        const res = await axios({
          url: `/api/contact/${id}`,
          method: 'DELETE',
          headers: {
            'auth-token': localStorage.getItem('auth-token'),
          },
        });
        if (res) {
        this.contacts = this.contacts.filter((contact) => {
            return contact._id !== id;
          });
          this.message = true;
          this.messageContent = "Contact deleted !";
        }
      },
      fullName(fn,ln){
        return fn+" "+ln
      },
      async newContact(newContact){
        const res = await axios.post('/api/contact/add',
          newContact,{
          headers: {
            'auth-token': localStorage.getItem('auth-token'),
          },
          }
        )
        if (res) {
          this.contacts.push(res.data)
          this.add = false;
          this.message = true;
          this.messageContent = "Contact added !";
        }

      }
      
    }    ,
    async created() {
      await axios({
        url: 'api/contact/',
        method: 'GET',
        headers: {
          'auth-token': localStorage.getItem('auth-token'),
        },
      }).then((res) => { 
        this.contacts = res.data.sort((a,b)=>{
          return a.firstName.toLowerCase() > b.firstName.toLowerCase()
        })
      });
      this.message = false;
      
    },
    computed: {
      filterContact(){
        return this.contacts.filter(contact=>{
          return contact.firstName.toLowerCase().match(this.search.toLowerCase()) || contact.lastName.toLowerCase().match(this.search.toLowerCase())||contact.phoneNumber.match(this.search)
        })
      },
      noContactList(){
        if(this.contacts.length<1){
          return true;
        }
        else{
          return false;
        }
      }
    },
  };
</script>
<style></style>
